<template>
<div class="fw">
   
    <div class="topBar">
        <i class="fas fa-layer-minus toggle" @click="toggleMenu"></i>
        <img src="../assets/logo-3.png" class="logo">
    </div>
    <div class="sideBar" v-bind:class = "{'showMenu':showMenu}">
        <ul>
            <li @click="toggleMenu" v-bind:class="{'active':$route.path == '/'}">
                <router-link :to="{path:'/'}">
                    <img src="../assets/icons/house.svg">
                    <span>Home</span>
                </router-link>
            </li>
            <li @click="toggleMenu" v-bind:class="{'active':$route.path == '/farms'}">
                <router-link :to="{path:'/farms'}">
                    <img src="../assets/icons/harvest.png">
                    <span>Farm</span>
                </router-link>
            </li>
            <li @click="toggleMenu" v-bind:class="{'active':$route.path == '/pool'}">
                <router-link :to="{path:'/pool'}">
                    <img src="../assets/icons/bottle.png">
                    <span>Pools</span>
                </router-link>
            </li>
            <li @click="toggleMenu" v-bind:class="{'active':$route.path == '/bridge'}">
                <router-link :to="{path:'/bridge'}">
                    <img src="../assets/icons/bottle.png">
                    <span>Bridge</span>
                </router-link>
            </li>
            <li  @click="toggleMenu">
                <a href="http://docs.starseed.exchange" target="_new">
                    <img src="../assets/icons/document.png">
                    <span>Docs</span>
                </a>
            </li>
            <li  @click="toggleMenu">
                <img src="../assets/icons/audit.png">
                <span>Audits</span>
            </li>
        </ul>
        <div class="earning">
          $ 0.00
        </div>

    </div>
    <div class="connect">
        <button @click="MetaMask"><i class="fas fa-network-wired"></i>Connect</button>
    </div>
    
</div>
</template>

<script>
import * as THREE from 'three';
import axios from "axios"
export default {
    data() {
        return {
            camera: false,
            scene: false,
            renderer: false,
            stars: [],
            account: false,
            showMenu:false
        }
    },
    components: {

    },
    watch: {
        "$route.params": {
            handler(newValue, oldValue) {
                console.log(this.$route.path)
            },
            immediate: true
        }
    },
    created() {
        this.webGL()
    },
    methods: {
        toggleMenu(){
            if(this.showMenu)
            this.showMenu = false
            else
            this.showMenu = true
        },
        webGL() {
           
            var init = () => {

                //camera
                this.camera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 1, 1000);
                this.camera.position.z = 5;

                //scene
                this.scene = new THREE.Scene();

                //renderer
                this.renderer = new THREE.WebGLRenderer({
                    alpha: true
                });
                this.renderer.setSize(window.innerWidth, window.innerHeight);
                document.body.prepend(this.renderer.domElement);
            }

            var addSphere = () => {
                for (var z = -1000; z < 1000; z += 20) {
                    var geometry = new THREE.SphereGeometry(0.5, 32, 32)
                    var material = new THREE.MeshBasicMaterial({
                        color: 0xffffff
                    });
                    var sphere = new THREE.Mesh(geometry, material)
                    sphere.position.x = Math.random() * 1000 - 500;
                    sphere.position.y = Math.random() * 1000 - 500;
                    sphere.position.z = z;
                    sphere.scale.x = sphere.scale.y = 2;
                    this.scene.add(sphere);
                    this.stars.push(sphere);
                }
            }

            var animateStars = () => {
                for (var i = 0; i < this.stars.length; i++) {
                    var star = this.stars[i];
                    star.position.z += i / 10;
                    if (star.position.z > 1000) star.position.z -= 2000;
                }
            }

            var render = () => {
                requestAnimationFrame(render);
                this.renderer.render(this.scene, this.camera);
                animateStars();
                
            }

            init();
            addSphere();
            render();
        },
        async MetaMask() {
            const accounts = await ethereum.request({
                method: 'eth_requestAccounts'
            });
            document.getElementById("account").innerHTML = accounts[0];
        }
    }
}
</script>
